.progress-box {
  width: 100%;
  background: #ccc;
  height: 3px;
  cursor: pointer;
}
.progress-box .progress {
  width: 10%;
  height: 3px;
  background: #f00;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}



